.date-picker {
    margin-bottom: 7px !important;
}

.date-picker input {
    border-radius: 7px;
    margin-bottom: 0;
    padding: 12px 11px;
    color: #333;
    font-weight: 300;
    font-size: 14px;
    background-color: #FFF;
}

.date-picker fieldset {
    border-color: #abc
}

.date-picker label {
    color: #004159 !important;
    line-height: 16px;
    font-size: 14px;
    margin-top: -2px;
}

.date-picker label[data-shrink="true"] {
    line-height: 24px;
    font-size: 16px;
}

.date-picker .MuiOutlinedInput-root.Mui-focused fieldset {
    border-color: #004159 !important;
    border-width: 1px !important;
}